import React, { Component } from 'react';
import { Table } from 'antd';
import config from 'src/commons/config-hoc';
import { tableRowDraggable } from 'src/library/components';

const GragTable = tableRowDraggable(Table);

@config({ path: '/drag' })
export default class TT extends Component {
    state = {
        dataSource: [
            { id: '1', name: '张三', age: 23 },
            { id: '2', name: '李四', age: 24 },
            { id: '3', name: '王五', age: 25 },
            { id: '4', name: '宋六', age: 26 },
        ],
    };

    columns = [
        { title: '用户名', dataIndex: 'name' },
        { title: '年龄', dataIndex: 'age' },
        { title: '', dataIndex: 'age' },
    ];

    handleSortEnd = ({ newIndex, oldIndex }) => {
        const { dataSource } = this.state;
        dataSource.splice(newIndex, 0, ...dataSource.splice(oldIndex, 1));

        this.setState({ dataSource: [...dataSource] });
    };
    render() {

        return (
            <GragTable
                columns={this.columns}
                dataSource={this.state.dataSource}
                rowKey="id"
                onSortEnd={this.handleSortEnd}
            />
        );
    }
}
